import { PopoverPlacement } from './popoverPlacement';

export enum PopoverSize {
    ExtraSmall = 'extra-small',
    Small = 'small',
    Medium = 'medium',
}

export enum PopoverSlot {
    ACTION_SLOT = 'action-slot',
    HEADER_SLOT = 'header-slot',
}

export enum PopoverEvent {
    TOGGLE = 'popover-toggle',
}

export interface PopoverProperties {
    open?: boolean;
    size?: PopoverSize | string;
    required?: boolean;
    resizable?: boolean;
    draggable?: boolean;

    objectId?: string;
    object?: HTMLElement;
    dockTo?: HTMLElement;

    clickOutsideClose?: boolean;
    placement?: PopoverPlacement | string;
    forcePlacement?: PopoverPlacement | string;
    staticPlacement?: boolean;
    contentScroll?: boolean;
    onPopoverToggle?: any;
}
