import { css, cssSelector, cssVariableAsVar, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const popoverStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);
    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: inline-block;
                position: fixed;
                z-index: var(--popover-z, 1000);
                left: var(--popover-left);
                right: var(--popover-right);
                top: var(--popover-top);
                bottom: var(--popover-bottom);
                --header-height: 48px;
                --footer-height: 0px;
            }

            ${is('open')} {
                display: flex;
            }

            ${is('!open')} {
                display: none;
            }

            ${is('!no-footer')} {
                --footer-height: 72px;
            }

            ${root} {
                z-index: 1001;
                height: auto;
                display: flex;
                flex-direction: column;
                background: ${cssVariableAsVar('color_primary_2')};
                border: 1px solid ${cssVariableAsVar('color_secondary_2')};
                box-shadow: 0px 4px 4px 0px #0000000f;
            }

            ${child('content')} {
                display: flex;
                flex-direction: column;
            }

            ${child('header')} {
                flex-basis: var(--header-height);
                flex-shrink: 0;
                border-bottom: 1px solid ${cssVariableAsVar('color_secondary_3')};
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                padding: 0 12px 0 24px;
                font-size: 16px;
            }

            ${is('content-scroll')} ${child('scrollbar')} {
                height: calc(100% - (var(--header-height) + var(--footer-height)));
                padding: var(--popover-content-padding, 16px 24px 0px 24px);
            }

            ${is('!content-scroll', '!required')} ${child('content')} {
                flex-basis: 100%;
                padding: var(--popover-content-padding, 16px 24px);
            }

            ${is('!content-scroll', 'required')} ${child('content')} {
                flex-basis: 100%;
                padding: var(--popover-content-padding, 16px 24px 0px 24px);
            }

            ${child('footer')} {
                flex-basis: var(--footer-height);
                display: flex;
                align-items: center;
                justify-content: flex-end;
                flex-shrink: 0;
                padding: 0 24px;
            }

            ${is('resizable')} {
                resize: both;
                overflow: auto;
            }

            ${is('custom-draggable')} ${child('header')} {
                cursor: grab;
            }

            ${is('dragging', 'custom-draggable')} ${child('header')} {
                cursor: grabbing;
            }

            ${is('content-scroll')} ${child('content')} {
                min-height: var(--popover-content-min-height, 35vh);
                max-height: var(--popover-content-max-height, 35vh);
            }

            ${is('medium')} {
                width: 576px;
            }

            ${is('small')} {
                width: 418px;
            }

            ${is('extra-small')} {
                width: 350px;
            }
        `,
    ];
};
