import {
    BaseElement,
    booleanConverter,
    createClassName,
    cssVariables,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { popoverStyles } from './popoverStyle';
import { PopoverEvent, PopoverProperties, PopoverSize, PopoverSlot } from './popoverProperties';
import cancel from '@umetrics/sartorius-ui-icon/dist/icons/cancel';
import { PopoverPlacement } from './popoverPlacement';
import { ResizeAwareMixin } from '@umetrics/sartorius-ui-mixins';

@defineElement({ tagName: 'popover', styles: popoverStyles, version })
export class Popover extends ResizeAwareMixin(BaseElement) implements PopoverProperties {
    // Static members
    private static Button;
    private static Icon;
    private static Scrollbar;
    // Private members

    // Protected members

    // Public members
    @property({ type: PopoverSize })
    size = PopoverSize.Medium;
    @property({ type: Boolean, converter: booleanConverter })
    required = false;
    @property({ type: Boolean, converter: booleanConverter })
    resizable = false;
    @property({ type: Boolean, converter: booleanConverter })
    draggable = false;

    @property({ type: Object, attribute: false, reflect: false })
    public object?: HTMLElement;
    @property({ type: String, reflect: true, attribute: 'object-id' })
    public objectId?: string;
    @property({ type: Object, attribute: false, reflect: false })
    public dockTo?: HTMLElement;

    @property({ type: Boolean, converter: booleanConverter, attribute: 'click-outside-close' })
    clickOutsideClose = false;
    @property({ type: PopoverPlacement })
    placement;
    @property({ type: PopoverPlacement, attribute: 'force-placement' })
    forcePlacement;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'static-placement' })
    staticPlacement = false;
    @property({ type: Boolean, attribute: 'content-scroll', converter: booleanConverter })
    contentScroll = false;
    @query('.container')
    private _container;
    @query('[class$="-scrollbar"]')
    private _scrollView;
    @query('[class$="-footer"]')
    private _footer;

    @property({ type: Boolean, converter: booleanConverter, reflect: true })
    get open() {
        return this._open;
    }

    set open(val) {
        const prev = this._open;
        if (val === prev) return;
        this._open = val;
        this._toggle();
        this.requestUpdate('open', prev);
    }

    @state()
    private _dragging = false;
    private _dragPos: { x: number; y: number } = { x: 0, y: 0 };
    private _open = false;

    // Memory for ResizeObserver since it always trigger a first time.
    private _lastWidth;
    private _lastHeight;

    constructor() {
        super();
        this._handleScroll = this._handleScroll.bind(this);
    }

    connectedCallback() {
        const imports: any[] = [];
        this._elementDrag = this._elementDrag.bind(this);
        this._closeDragElement = this._closeDragElement.bind(this);
        this._handleKeyDown = this._handleKeyDown.bind(this);
        this._toggle = this._toggle.bind(this);
        this._toggleOpen = this._toggleOpen.bind(this);
        this._positionPopover = this._positionPopover.bind(this);
        this._decidePlacement = this._decidePlacement.bind(this);
        this._handleWindowClick = this._handleWindowClick.bind(this);
        imports.push({ module: () => import('@umetrics/sartorius-ui-button'), elem: ['Button'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-scrollbar'), elem: ['Scrollbar'] });

        Popover.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.addEventListener('scroll', this._handleScroll);
        });
    }

    disconnectedCallback() {
        this.removeEventListener('scroll', this._handleScroll);
        super.disconnectedCallback();
    }

    async firstUpdated(c) {
        super.firstUpdated(c);

        requestAnimationFrame(() => {
            const content = this.shadowRoot!.querySelector('[class$="-content"]');
            if (this._scrollView && content && this.required && content.clientHeight > this._scrollView.clientHeight) {
                this._footer.style.borderTop = `1px solid ${cssVariables.color_secondary_3}`;
            }
        });

        if (this.objectId && !this.object && this.parentElement) {
            const parent = this.parentElement!;
            this.object = parent.querySelector(`#${this.objectId}`) as HTMLElement;
        }
        if (this.object) {
            this.object.addEventListener('click', this._toggleOpen);
        }
        if (this.open) {
            this._toggle();
        }
    }

    protected update(changedProperties) {
        if (changedProperties.has('objectId')) {
            this._updateObjectId(changedProperties.get('objectId'));
        }
        if (changedProperties.has('object')) {
            this._updateObject(changedProperties.get('object'));
        }
        super.update(changedProperties);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            [this.size]: true,
            open: this.open,
            resizable: this.resizable,
            ['custom-draggable']: this.draggable,
            dragging: this._dragging,
            required: this.required,
            'content-scroll': this.contentScroll,
            'no-footer': !this.required,
        });

        const closeHtml = this.required
            ? html``
            : html`
                <${Popover.Button.tag} @click="${this._toggleOpen}" type="tertiary" compact="true">
                    <${Popover.Icon.tag} slot="icon-slot" class="${baseClassName}-close-button-icon"
                                         icon="${cancel}"></${Popover.Icon.tag}>
                </${Popover.Button.tag}>`;

        const actionHtml = this.required
            ? html` <div class="${baseClassName}-footer">
                  <slot name="${PopoverSlot.ACTION_SLOT}"></slot>
              </div>`
            : html``;
        const innerContent = html` <div class="${baseClassName}-content">
            <slot></slot>
        </div>`;
        const content = this.contentScroll
            ? html`
            <${Popover.Scrollbar.tag} class="${baseClassName}-scrollbar" data-testid='popover-scrollbar'>
                ${innerContent}
            </${Popover.Scrollbar.tag}>        
        `
            : innerContent;

        return html`
            <div
                class="${rootClasses} container"
                @click=${(e) => {
                    if (this.object == null) e.stopPropagation();
                }}
            >
                <div
                    class="${baseClassName}-header"
                    @mousedown=${this.draggable ? (e) => this._dragMouseDown(e) : null}
                >
                    <slot name="${PopoverSlot.HEADER_SLOT}"></slot>
                    ${closeHtml}
                </div>
                ${content} ${actionHtml}
            </div>
        `;
    }

    protected override _resizeObserverElem() {
        return this;
    }

    protected override _resizeObserverCallback(entries: ResizeObserverEntry[]) {
        if (!entries?.length) {
            return;
        }
        const { clientWidth: newWidth, clientHeight: newHeight } = entries[0].target;
        if (newWidth <= 0 || newHeight <= 0) {
            return;
        }

        const dimensionChanged =
            (newWidth !== this._lastWidth && this._lastWidth > 0) ||
            (newHeight !== this._lastHeight && this._lastHeight > 0);
        this._lastHeight = newHeight;
        this._lastWidth = newWidth;
        if (dimensionChanged) {
            this._cancelAnyInvertedPositioning();
        }
    }

    private async _handleScroll() {
        if (this._scrollView && this.required) {
            // Could be without scrollbar in this, but further down the dom...
            if (this._scrollView.getScrollTop() >= this._scrollView.getScrollHeight() - this._scrollView.clientHeight) {
                this._footer.style.borderTop = '1px solid transparent';
            } else {
                this._footer.style.borderTop = `1px solid ${cssVariables.color_secondary_3}`;
            }
        }
    }

    private _updateObjectId(_) {
        if (this.objectId && this.parentElement) {
            this.object = this.parentElement.querySelector(`#${this.objectId}`) as HTMLElement;
        } else {
            this.object = undefined;
        }
    }

    private _updateObject(prevObject) {
        if (prevObject !== undefined) {
            prevObject.removeEventListener('click', this._toggleOpen);
        }
        if (this.object) {
            this.object.addEventListener('click', this._toggleOpen);
        }
    }

    private _toggleOpen() {
        this.open = !this.open;

        // Reset scroll position
        requestAnimationFrame(() => {
            if (this.contentScroll) {
                this._scrollView?.scrollTo({ top: 0, behavior: 'instant' });
            }
        });
    }

    private _toggle() {
        if (this.open && !this.staticPlacement) {
            window.addEventListener('keydown', this._handleKeyDown);
            this._positionPopover();

            if (this.clickOutsideClose && !this.required) {
                window.addEventListener('click', this._handleWindowClick, true);
            }
        } else {
            window.removeEventListener('keydown', this._handleKeyDown);
            this._uninstallResizeObserver(this);
            if (this.clickOutsideClose && !this.required) {
                window.removeEventListener('click', this._handleWindowClick, true);
            }
        }
        this.dispatchEvent(new CustomEvent(PopoverEvent.TOGGLE, { detail: { open: this.open } }));
    }

    private _handleKeyDown(e: KeyboardEvent) {
        if (e.code === 'Escape' && !this.required) {
            this._toggleOpen();
        }
    }

    private _dragMouseDown(e) {
        if (e.target.className.indexOf('-close-button-icon') > 0) {
            return;
        }
        e.preventDefault();

        // get the mouse cursor position at startup:
        this._dragPos = { x: e.clientX, y: e.clientY };

        document.addEventListener('mousemove', this._elementDrag);
        document.addEventListener('mouseup', this._closeDragElement);
        this._dragging = true;
    }

    private _elementDrag(e) {
        e.preventDefault();
        const pos1 = this._dragPos.x - e.clientX;
        const pos2 = this._dragPos.y - e.clientY;
        this._dragPos.x = e.clientX;
        this._dragPos.y = e.clientY;
        this.style.top = this.offsetTop - pos2 + 'px';
        this.style.left = this.offsetLeft - pos1 + 'px';
    }

    private _closeDragElement() {
        document.removeEventListener('mousemove', this._elementDrag);
        document.removeEventListener('mouseup', this._closeDragElement);
        this._dragging = false;
        this._dragPos = { x: 0, y: 0 };
    }

    private _handleWindowClick(e) {
        if (
            e.target !== this.object &&
            e.composedPath().indexOf(this.object) === -1 &&
            !e.composedPath().includes(this)
        ) {
            this._toggleOpen();
        }
    }

    private _calculateSpace(triggerRect: DOMRect) {
        const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

        const spaceAbove = triggerRect.top;
        const spaceBelow = windowHeight - triggerRect.bottom;
        const spaceToLeft = triggerRect.left;
        const spaceToRight = windowWidth - triggerRect.right;
        return { spaceAbove, spaceBelow, spaceToLeft, spaceToRight };
    }

    private _decidePlacement() {
        return new Promise((resolve) => {
            const triggerObject = this.dockTo ?? this.object;
            const triggerRect = triggerObject!.getBoundingClientRect();
            const { spaceAbove, spaceBelow, spaceToLeft, spaceToRight } = this._calculateSpace(triggerRect);

            const maxSpace = Math.max(spaceAbove, spaceBelow, spaceToLeft, spaceToRight);
            let placement;

            switch (maxSpace) {
                case spaceAbove:
                    placement =
                        spaceToLeft > spaceToRight
                            ? PopoverPlacement.AboveRightAligned
                            : PopoverPlacement.AboveLeftAligned;
                    break;
                case spaceBelow:
                    placement =
                        spaceToLeft > spaceToRight
                            ? PopoverPlacement.BelowRightAligned
                            : PopoverPlacement.BelowLeftAligned;
                    break;
                case spaceToLeft:
                    placement =
                        spaceAbove > spaceBelow
                            ? PopoverPlacement.LeftSideBottomAligned
                            : PopoverPlacement.LeftSideTopAligned;
                    break;
                case spaceToRight:
                    placement =
                        spaceAbove > spaceBelow
                            ? PopoverPlacement.RightSideBottomAligned
                            : PopoverPlacement.RightSideTopAligned;
                    break;
            }
            resolve(placement);
        });
    }

    private _positionPopoverVerticallyIfOutsideWindow(windowHeight: number) {
        // Ensure that the popover is not outside the window vertically
        const rect = this._container.getBoundingClientRect();
        // Extra padding to ensure that the popover is not too close to the edge of the window
        const padding = 24;
        if (rect.top < 0) {
            this.style.top = padding + 0 + 'px';
        } else if (rect.bottom > windowHeight) {
            this.style.top = windowHeight - rect.height - padding + 'px';
        }
    }

    private async _positionPopover() {
        if (!this.forcePlacement) this.placement = await this._decidePlacement();
        else this.placement = this.forcePlacement;

        requestAnimationFrame(() => {
            const triggerObject = this.dockTo ?? this.object;
            if (triggerObject) {
                const triggerRect = triggerObject.getBoundingClientRect();

                // Reset styles
                this.style.removeProperty('--popover-left');
                this.style.removeProperty('--popover-right');
                this.style.removeProperty('--popover-top');
                this.style.removeProperty('--popover-bottom');
                const windowWidth =
                    window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
                const windowHeight =
                    window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

                const spaceBelow = windowHeight - triggerRect.bottom;
                const spaceToLeft = triggerRect.left;
                const spaceToRight = windowWidth - triggerRect.right;
                let invert = false;
                switch (this.placement) {
                    case PopoverPlacement.BelowRightAligned:
                        this.style.setProperty('--popover-right', `${spaceToRight}px`);
                        this.style.setProperty('--popover-top', `${triggerRect.top + triggerRect.height}px`);
                        invert = true;
                        break;
                    case PopoverPlacement.BelowLeftAligned:
                        this.style.setProperty('--popover-left', `${spaceToLeft}px`);
                        this.style.setProperty('--popover-top', `${triggerRect.top + triggerRect.height}px`);
                        break;
                    case PopoverPlacement.RightSideTopAligned:
                        this.style.setProperty('--popover-left', `${triggerRect.left + triggerRect.width}px`);
                        this.style.setProperty('--popover-top', `${triggerRect.top}px`);
                        break;
                    case PopoverPlacement.RightSideBottomAligned:
                        this.style.setProperty('--popover-left', `${triggerRect.left + triggerRect.width}px`);
                        this.style.setProperty('--popover-bottom', `${spaceBelow}px`);
                        invert = true;
                        break;
                    case PopoverPlacement.LeftSideTopAligned:
                        this.style.setProperty('--popover-right', `${spaceToRight + triggerRect.width}px`);
                        this.style.setProperty('--popover-top', `${triggerRect.top}px`);
                        invert = true;
                        break;
                    case PopoverPlacement.LeftSideBottomAligned:
                        this.style.setProperty('--popover-right', `${spaceToRight + triggerRect.width}px`);
                        this.style.setProperty('--popover-bottom', `${spaceBelow}px`);
                        invert = true;
                        break;
                    case PopoverPlacement.AboveRightAligned:
                        this.style.setProperty('--popover-right', `${spaceToRight}px`);
                        this.style.setProperty('--popover-bottom', `${spaceBelow + triggerRect.height}px`);
                        invert = true;
                        break;
                    case PopoverPlacement.AboveLeftAligned:
                        this.style.setProperty('--popover-left', `${spaceToLeft}px`);
                        this.style.setProperty('--popover-bottom', `${spaceBelow + triggerRect.height}px`);
                        invert = true;
                        break;
                }

                this._positionPopoverVerticallyIfOutsideWindow(windowHeight);

                if (this.resizable && invert) {
                    // Right/bottom positioning and resize will not go well together, so install resizeObserver that
                    // reset positioning in case of resize.
                    this._installResizeObserver(this);
                }
            }
        });
    }

    private _cancelAnyInvertedPositioning() {
        const rect = this.getBoundingClientRect();
        if (this.style.getPropertyValue('--popover-bottom')) {
            this.style.removeProperty('--popover-bottom');
            this.style.top = rect.top + 'px';
        }
        if (this.style.getPropertyValue('--popover-right')) {
            this.style.removeProperty('--popover-right');
            this.style.left = rect.left + 'px';
        }
    }
}
